import * as React from 'react'
import { Grid, IGridHeader, IGridColumn, _, gridUtil, } from '@ZenComponents/grid/gridExports'

import * as mpxTableUtil from './mpxTableUtil'

import { GridTitleBar } from './GridTitleBar'

export class InputProducts extends React.Component<{}, {}> {
  state = {
    showAdvanced: false,
    headers: [] as IGridHeader[],
    data: [],
    selectedRows: [],
  }

  componentWillMount() {
    let headers = this._buildHeaders(this.state.showAdvanced)
    this.setState({ headers })
    this._fetch()
  }

  _fetch = async () => {
    let data = await mpxTableUtil.fetchTableJson('products')
    // Filter out NONE row
    data = _.filter(data, c => c.proddesc !== 'NONE')
    this.setState({ data })
  }

  _buildHeaders = showAdvanced => {
    let headers: IGridHeader[] = [
      {
        check: true,
      },
      {
        label: 'ID',
        dataItem: 'prodid',
        width: 40,
      },
      {
        label: 'Name',
        dataItem: 'proddesc',
        tooltip: 'Name of the product, must be unique',
      },
      {
        label: 'Product Family',
        dataItem: 'proddept',
        tooltip: 'Name to group products by',
      },
      {
        label: 'Demand',
        dataItem: 'enddemd',
        tooltip:
          'The total customer demand for the product over the forecast period',
        width: 90,
      },
      {
        label: 'Lot Size',
        dataItem: 'lotsiz',
        tooltip: 'Average lot size used during production of the product',
        width: 90,
      },
      {
        label: 'Batch Size',
        dataItem: 'transferbatch',
        tooltip:
          'Number of pieces completed and pushed on to next operation before whole lot is completed. A value of -1 means that pieces wait until the entire batch is finished before moving.',
        width: 90,
        tag: 'advanced',
      },
      {
        label: 'Gather Batches?',
        dataItem: 'tbatchgather',
        editor: 'check',
        tooltip:
          'Gather all of the pieces transfer batches back into a full lot before moving them to a new product or move the transfer batches to the other product as batches are finished.',
        tag: 'advanced',
      },
      {
        label: 'Make to Stock',
        dataItem: 'makestock',
        editor: 'check',
        tooltip: 'Sets lead time for these parts to 0 in IBOM output page.',
        tag: 'advanced',
      },
      {
        label: 'Priority',
        dataItem: 'value',
        tooltip:
          'Weighting the relative importance of different products with larger numbers meaning the product is more valuable.',
        width: 90,
        tag: 'advanced',
      },
      {
        label: 'Variability Multiplier',
        dataItem: 'variability',
        tooltip:
          'A multiplier for how much of the Variability in Product Times affect this machine group.',
        width: 90,
        tag: 'advanced',
      },
      {
        label: 'Lot Size Multiplier',
        dataItem: 'lotsizefac',
        tooltip: 'A multiplier to affect the Batch Size for the product.',
        width: 90,
        tag: 'advanced',
      },
      {
        label: 'Demand Multiplier',
        dataItem: 'demandfac',
        tooltip: 'A multiplier to affect the Demand for the product.',
        width: 90,
        tag: 'advanced',
      },
      {
        label: 'Comment',
        dataItem: 'prodcomment',
      },
    ]

    if (!showAdvanced) {
      headers = _.filter(headers, c => c.tag !== 'advanced')
    }

    return headers
  }

  _onUpdateCell = (newValue: any, column: IGridColumn, dataRow: any) => {
    console.log('update', newValue, column.header.dataItem, dataRow.id)
    dataRow[column.header.dataItem] = newValue
    this.setState({ data: this.state.data })

    mpxTableUtil.updateTable(
      'products',
      dataRow.prodid,
      column.header.dataItem,
      newValue
    )
  }

  _onToggleShowAdvanced = () => {
    console.log('toggle show advanced')
    let showAdvanced = !this.state.showAdvanced
    let headers = this._buildHeaders(showAdvanced)
    this.setState({ showAdvanced, headers })
  }

  _onAddRow = async () => {
    await mpxTableUtil.fetchAddRow('products')
    await this._fetch()
  }

  _onSelectRow = (dataRow, isSelected, mode) => {
    this.setState({
      selectedRows: gridUtil.selectRow(
        this.state.selectedRows,
        dataRow,
        isSelected,
        mode
      ),
    })
  }

  render() {
    return (
      <div>
        <GridTitleBar
          title="Products"
          showAdvanced={this.state.showAdvanced}
          onToggleShowAdvanced={this._onToggleShowAdvanced}
          onAdd={this._onAddRow}
          onDeleteSelected={null}
          hasSelected={this.state.selectedRows.length > 0}
        />

        <Grid
          headers={this.state.headers}
          data={this.state.data}
          onUpdateCell={this._onUpdateCell}
          selectedRows={this.state.selectedRows}
          onSelectRow={this._onSelectRow}
        />
      </div>
    )
  }
}
