import * as React from 'react'
import { Json } from '@ZenComponents/common/Json'
import {
  Grid,
  IGridHeader,
  IGridColumn,
  _,
  gridUtil,
} from '@ZenComponents/grid/gridExports'

import * as mpxTableUtil from './mpxTableUtil'

import { GridTitleBar } from './GridTitleBar'

export class InputEquipment extends React.Component<{}, {}> {
  state = {
    data: [],
    selectedRows: [],
    laborOptions: [],
    showAdvanced: false,
    headers: [] as IGridHeader[],
  }

  componentWillMount() {
    this._fetch()
  }

  _fetch = async () => {
    let data = await mpxTableUtil.fetchTableJson('equipment')
    // Filter out NONE row
    data = _.filter(data, c => c.equipdesc !== 'NONE')
    let laborOptions = await mpxTableUtil.selectList('labor', 'labordesc')
    let headers = this._buildHeaders(this.state.showAdvanced, laborOptions)
    this.setState({ data, laborOptions, headers })
  }

  _buildHeaders = (showAdvanced, laborOptions) => {
    let headers: IGridHeader[] = [
      {
        check: true,
      },
      {
        label: 'ID',
        dataItem: 'equipid',
        width: 40,
      },
      {
        label: 'Name',
        dataItem: 'equipdesc',
        tooltip: 'Name of the equipment group (each row must be unique).',
      },
      {
        label: 'Dept',
        dataItem: 'equipdept',
        tooltip: 'Department of the equipment group (for user only).',
      },
      {
        label: 'Type',
        dataItem: 'equiptypename',
        options: [
          { label: '-', value: 'null' },
          { label: 'Standard', value: 'Standard' },
          { label: 'Delay', value: 'Delay' },
        ],
        // dataItem: 'equiptype',
        // options: [
        //   { label: '-', value: 'null' },
        //   { label: 'Standard', value: '0' },
        //   { label: 'Delay', value: '1' },
        // ],
        tooltip:
          'Standard has a finite number of machines and Delay has infinite number of machines and work is immediately processed',
      },
      {
        label: 'Qty',
        dataItem: 'grpsiz',
        tooltip: 'The number of equipment in the group (at least 1).',
        width: 90,
      },
      {
        label: 'Overtime %',
        dataItem: 'ot',
        tooltip:
          'What extra percent of the scheduled time this equipment group can be worked.',
        width: 90,
      },
      {
        label: 'MTTF',
        dataItem: 'mtf',
        tooltip:
          'The average amount of time that the equipment can run before breaking, measured in the Operations Time Units selected on the General Input Page.',
        width: 90,
      },
      {
        label: 'MTTR',
        dataItem: 'mtr',
        tooltip:
          'The average amount of time that the equipment takes to be repaired, measured in the Operations Time Units selected on the General Input Page.',
        width: 90,
      },
      {
        label: 'Labor',
        dataItem: 'labordesc',
        options: gridUtil.mapStringsArrayToLabelValueArray(laborOptions),
        tooltip: 'The labor group that operates the equipment.',
      },
      {
        label: 'Setup Time Multiplier',
        dataItem: 'setup',
        tooltip:
          'A multiplier for how long of the input set-up time the machine group takes.',
        width: 90,
        tag: 'advanced',
      },
      {
        label: 'Run Time Multiplier',
        dataItem: 'run',
        tooltip:
          'A multiplier for how long of the input run time the machine group takes. This is useful to quickly effect ALL run times that the machine group does.',
        width: 90,
        tag: 'advanced',
      },
      {
        label: 'Variability Multiplier',
        dataItem: 'varbility',
        tooltip:
          'A multiplier for how much of the Variability in Product Times affect this machine group.',
        width: 90,
        tag: 'advanced',
      },
      {
        label: 'Comment',
        dataItem: 'EqComment',
      },
    ]

    if (!showAdvanced) {
      headers = _.filter(headers, c => c.tag !== 'advanced')
    }

    return headers
  }

  _onUpdateCell = (newValue: any, column: IGridColumn, dataRow: any) => {
    console.log('update', newValue, column.header.dataItem, dataRow.id)
    dataRow[column.header.dataItem] = newValue

    if (column.header.dataItem === 'equiptypename') {
      // Update our Qty (business logic)
      let newQty = 1
      if (newValue === 'Delay') {
        newQty = -1
      } else {
        newQty = 1
      }
      dataRow['grpsiz'] = newQty
      mpxTableUtil.updateTable('equipment', dataRow.equipid, 'grpsiz', newQty)
    }

    this.setState({ data: this.state.data })

    mpxTableUtil.updateTable(
      'equipment',
      dataRow.equipid,
      column.header.dataItem,
      newValue
    )
  }

  _onDeleteSelectedRows = () => {
    console.log('delete', this.state.selectedRows)
    let data = this.state.data
    // TODO: do ajax calls
    _.remove(data, (c: any) => _.find(this.state.selectedRows, d => c === d))
    this.setState({ data: this.state.data, selectedRows: [] })
  }

  _onToggleShowAdvanced = () => {
    console.log('toggle show advanced')
    let showAdvanced = !this.state.showAdvanced
    let headers = this._buildHeaders(showAdvanced, this.state.laborOptions)
    this.setState({ showAdvanced, headers })
  }

  _onSelectRow = (dataRow, isSelected, mode) => {
    this.setState({
      selectedRows: gridUtil.selectRow(
        this.state.selectedRows,
        dataRow,
        isSelected,
        mode
      ),
    })
  }

  render() {
    return (
      <div>
        <GridTitleBar
          title="Equipment"
          showAdvanced={this.state.showAdvanced}
          onToggleShowAdvanced={this._onToggleShowAdvanced}
          onAdd={null}
          onDeleteSelected={this._onDeleteSelectedRows}
          hasSelected={this.state.selectedRows.length > 0}
        />

        <Grid
          headers={this.state.headers}
          data={this.state.data}
          onUpdateCell={this._onUpdateCell}
          selectedRows={this.state.selectedRows}
          onSelectRow={this._onSelectRow}
        />
        {/* <Json data={this.state.headers} /> */}
        {/*  
        <Json data={this.state.laborOptions} />
        <Json
          data={gridUtil.mapStringsArrayToLabelValueArray(
            this.state.laborOptions
          )}
        />
        <Json data={this.state.data} /> */}
      </div>
    )
  }
}
